<style>
  .echarts {
    width: 100%;
    height: 100%;
  }
  .title{
    text-align: center;
    font-size:1.5em;
    font-weight: bold;
    margin-bottom: 10px;
  }
  .time{
    color: darkgray;
    font-size:1em;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
  }
</style>

<template>
  <div>
    <div class="title">雅居乐花园"四返"人员动态信息</div>
    <div class="time">(数据更新时间:{{date}})<div>
  <chart ref="region" :auto-resize="true" style="width:100%;" :options="region"/>
  <chart ref="isolate"  :auto-resize="true" style="width:100%;" :options="isolate"/>
  <chart ref="line"  :auto-resize="true"  style="width:100%;" :options="line"/>
  </div>
</template>

<script>
  import 'echarts/lib/chart/bar'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title.js'
  import 'echarts/lib/chart/pie'


  export default {
    name:"HelloWorld",
    data () {
      return {
        date:null,
        region: {
          title : {
            text: '"四返"人员出发地信息统计',
            subtext: '动态数据',
            x:'left'
          },
          tooltip : {
            trigger: 'item',
            formatter:"{a} <br/>{b} : {c}人 ({d}%)"
          },
          legend: {
            show: true,
            orient: 'horizontal',
            bottom: '20px',
            data: ['湖北省','一省四市','其他地区']
          },
          series : [
            {
              name: '此地区人员数量',
              type: 'pie',
              radius : '50%',
              center: ['50%', '50%'],
              label: {
                formatter: '{b}：{c}人 '}
              ,
              data:[
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        },
        isolate: {
          title : {
            text: '解除隔离人数占比',
            subtext: '动态数据',
            x:'left'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c}人 ({d}%)"
          },
          legend: {
            show: true,
            orient: 'horizontal',
            bottom: '20px',
            data: ['湖北隔离人数','一省四市隔离人数','其他隔离人数','解除隔离人数']
          },
          series : [
            {
              name: '此类人员数量',
              type: 'pie',
              radius : '50%',
              center: ['50%', '50%'],
              label: {
                formatter: '{b}：{c}人 '}
                ,
              data:[
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        },
        line:{
          title : {
            text: '隔离人数变化趋势',
            subtext: '动态数据',
            x:'left'
          },
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: [],
            label: {
              show:true,
              position:'top'
            }
            ,
            type: 'line'
          }]
        }

      }
    }
  ,
  methods: {
    loadData () {

      this.date = new Date();
      var that = this;

      this.$axios.get('https://www.geckos.top/api/peopleinfos')
        .then(res => {
          if (res.data!=null) {

            var peopleInfos = res.data;

            that.region.series[0].data=[
              {value:peopleInfos.hubei, name:'湖北省'},
              {value:peopleInfos.hotCity, name:'一省四市'},
              {value:peopleInfos.otherCity, name:'其他地区'}
            ];

            that.isolate.series[0].data=[
              {value:peopleInfos.hubeiIsolate, name:'湖北隔离人数'},
              {value:peopleInfos.hotCityIsolate, name:'一省四市隔离人数'},
              {value:peopleInfos.otherIsolate, name:'其他隔离人数'},
              {value:peopleInfos.notIsolate, name:'解除隔离人数'}
            ];

            that.line.xAxis.data= peopleInfos.dayliyDate;
            that.line.series[0].data=peopleInfos.dayliyIsolateNum;

            //that.$refs.region.mergeOptions(that.region);
          }
        })

    }
  },
  created () {
    this.loadData()
  },
  mounted () {

  }
  }
</script>



